import Vue from 'vue'
import Vuex from 'vuex'
import actions from './actions'
import mutations from './mutations'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    cleanerRobot: {
      mode: 1, // 模式
      switch: 0, // 开关状态
      battery: 0, // 电池电量
      lefttime1: 0, // 静音模式剩余时间
      lefttime2: 0, // 强效模式剩余时间
      time: 0, // 本次工作时间
      totaltime: 0, // 累计工作时间
      totalarea: 0, // 累计清扫面积
      distance: 0, // 本次慢跑路程
      totaldistance: 0, // 累计慢跑路程
      consume: 0, // 本次消耗卡路里
      totalconsume: 0, // 累计消耗卡路里
      filternet: 0, // 滤网剩余百分比
      filtercore: 0 // 滤芯剩余百分比
    },
    faultDetection: {
      status: 0, // 是否发生故障
      code: 0 // 故障状态码
    }
  },
  mutations,
  actions
})



