/**
 *  接收接口数据并更新数据内容
 */
export default{
  updateState (state, obj) {
    switch (obj.sid) {
      case 'cleanerRobot':
        if (typeof obj.data.mode !== 'undefined') {
          state.cleanerRobot.mode = obj.data.mode
        }
        if (typeof obj.data.switch !== 'undefined') {
          state.cleanerRobot.switch = obj.data.switch
        }
        if (typeof obj.data.battery !== 'undefined') {
          state.cleanerRobot.battery = obj.data.battery
        }
        if (typeof obj.data.lefttime1 !== 'undefined') {
          state.cleanerRobot.lefttime1 = obj.data.lefttime1
        }
        if (typeof obj.data.lefttime2 !== 'undefined') {
          state.cleanerRobot.lefttime2 = obj.data.lefttime2
        }
        if (typeof obj.data.time !== 'undefined') {
          state.cleanerRobot.time = obj.data.time
        }
        if (typeof obj.data.totaltime !== 'undefined') {
          state.cleanerRobot.totaltime = obj.data.totaltime
        }
        if (typeof obj.data.totalarea !== 'undefined') {
          state.cleanerRobot.totalarea = obj.data.totalarea
        }
        if (typeof obj.data.distance !== 'undefined') {
          state.cleanerRobot.distance = obj.data.distance
        }
        if (typeof obj.data.totaldistance !== 'undefined') {
          state.cleanerRobot.totaldistance = obj.data.totaldistance
        }
        if (typeof obj.data.consume !== 'undefined') {
          state.cleanerRobot.consume = obj.data.consume
        }
        if (typeof obj.data.totalconsume !== 'undefined') {
          state.cleanerRobot.totalconsume = obj.data.totalconsume
        }
        if (typeof obj.data.filternet !== 'undefined') {
          state.cleanerRobot.filternet = obj.data.filternet
        }
        if (typeof obj.data.filtercore !== 'undefined') {
          state.cleanerRobot.filtercore = obj.data.filtercore
        }
        break
      case 'faultDetection':
        if (typeof obj.data.code !== 'undefined') {
          state.faultDetection.code = obj.data.code
        }
        if (typeof obj.data.status !== 'undefined') {
          state.faultDetection.status = obj.data.status
        }
        break
    }
  }
}



