/**
 * 调用接口并将方法抛出
 */
import {jsonParse, catchFn, setCallback, setHilink} from '../common/common'
import mockData from '../mock/DevCacheInfoAll'
setHilink()
export default {
  initCallBack ({commit, state}) {
    let callbackArr = [
      'setSwitchStatusResult',
      'setModeResult',
      'setSecondTitleResult',
      'resetFiltercoreResult'
    ]
    setCallback(callbackArr)
    window.deviceEventCallback = (res) => {
      let data = jsonParse(res)
      console.log('deviceEventCallback执行后的数据：', data)
      commit('updateState', data)
    }
    window.getDevCacheInfoAllResult = (res) => {
      let data = jsonParse(res)
      console.log('getDevCacheInfoAllResult:', data)
      data.services.forEach((item) => {
        commit('updateState', item)
      })
    }
  },
  // 获取设备所有信息
  getDevCacheInfoAll () {
    catchFn(
      () => window.hilink.getDevCacheAll('0', '', 'getDevCacheInfoAllResult'),
      '获取设备所有信息',
      () => window.getDevCacheInfoAllResult(JSON.stringify(mockData))
    )
  },
  // 改变开关状态
  setSwitchStatus ({state}) {
    let data = {
      'cleanerRobot': {
        'switch': !state.cleanerRobot.switch
      }
    }
    window.Hilink.setDeviceInfo(data, 'setSwitchStatusResult')
  },
  // 改变模式
  setMode ({state}) {
    let data = {
      'cleanerRobot': {
        'mode': state.cleanerRobot.mode === 1 ? 2 : 1
      }
    }
    window.Hilink.setDeviceInfo(data, 'setModeResult')
  },
  // 改变二级标题
  setSecondTitle ({state}) {
    let data = {
      'cleanerRobot': {
        'mode': state.cleanerRobot.mode === 1 ? 2 : 1
      }
    }
    catchFn(
      () => window.hilink.modifyTitleStatus(data, 'setSecondTitleResult'),
      '改变二级标题',
      () => window.deviceEventCallback(JSON.stringify(data))
    )
  },
  // 复位滤芯
  resetFiltercore ({state}) {
    let data = {
      'cleanerRobot': {
        'filtercore': 100
      }
    }
    window.Hilink.setDeviceInfo(data, 'resetFiltercoreResult')
  }
}



