<template>
  <div id="app">
    <transition :name="transitionName">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import {htmlFontSize} from './common/common'
export default {
  name: 'App',
  data () {
    return {
      transitionName: ''
    }
  },
  created () {
    // 初始化回调，将方法下挂到window上
    this.$store.dispatch('initCallBack')
    // 获取设备信息
    this.$store.dispatch('getDevCacheInfoAll')
  },
  mounted () {
    // 字体适配
    htmlFontSize()
  },
  methods: {

  },
  computed: {

  },
  watch: {
    $route (to, from) {
      if (from.path !== '/') {
        if (to.path === '/sweeper') {
          this.transitionName = 'slide-right'
        } else {
          this.transitionName = 'slide-left'
        }
      }
    }
  }
}
</script>



