import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/sweeper'
    },
    {
      path: '/sweeper',
      name: 'sweeper',
      component: resolve => require(['@/components/sweeper'], resolve)
    },
    {
      path: '/errorInfo/:status',
      name: 'errorInfo',
      component: resolve => require(['@/components/errorInfo'], resolve)
    },
    {
      path: '/filter',
      name: 'filter',
      component: resolve => require(['@/components/filter'], resolve)
    },
    {
      path: '/statistics',
      name: 'statistics',
      component: resolve => require(['@/components/statistics'], resolve)
    }
  ]
})



