import mockData from '../mock/DevCacheInfoAll'

/**
 * 数据转换以及异常处理，并返回处理后的数据
 * @param {String} res 接口返回的字符串
 */
export function jsonParse (res) {
  let data
  try {
    data = JSON.parse(res)
  } catch (err) {
    let dataStr = res.replace(/:"{/g, ':{')
    dataStr = dataStr.replace(/}",/g, '},')
    dataStr = dataStr.replace(/\\/g, '')
    data = JSON.parse(dataStr)
  }
  return data
}

/**
 * try catch 函数集成精简
 * @param {Function} fn 需要异常处理的函数
 * @param {String} warnMsg 提示信息
 * @param {Function} fn2 异常之后执行的异常处理函数
 */
export function catchFn (fn, warnMsg, fn2) {
  try {
    fn()
  } catch (err) {
    fn2 && fn2()
    warnMsg = warnMsg || '未自定义的'
    console.warn(`${warnMsg}错误信息: ${err}`)
  }
}

/**
 * 回调数据处理
 * @param {String} res 接口返回的字符串
 * @param {String} msg 提示信息
 * @param {Function} fn1 成功后执行的函数
 * @param {Function} fn2 异常下执行的函数
 */
export function resFn (res, msg, fn1, fn2) {
  let data = jsonParse(res)
  if (data.errcode === 0) {
    console.info(`${msg}成功: ${data.errcode}`)
    fn1 && fn1()
  } else {
    console.warn(`${msg}错误信息编码: ${data.errcode}`)
    fn2 && fn2()
  }
}

/**
 * JSAPI回调函数生成
 * @param {Array} array 回调函数名数组
 * @param {Object} callBackList 回调函数处理的列表对象,如：{"getDevCacheAllResult":{"callback":callbackFn,"success":successFn,"error":errorFn}}
 * @param {Function} callBackFn 可选执行的回调函数
 */
export function setCallback (array, callBackList, callBackFn) {
  array.forEach((item) => {
    if (callBackList && callBackList[item]) {
      window[item] = (res) => {
        callBackFn && callBackFn()
        callBackList[item].callback && callBackList[item].callback(res)
        resFn(res, item, callBackList[item].success, callBackList[item].error)
      }
    } else {
      window[item] = (res) => {
        callBackFn && callBackFn()
        resFn(res, item)
      }
    }
  })
}

/**
 * 集成hilink接口，转化为Hilink
 * @param {Function} callbackFn 可选执行的回调函数
 * @param {Function} debugCallback 调试环境下的回调函数
 */
export function setHilink (callbackFn, debugCallback) {
  window.Hilink = {
    getDevCacheAll (fn) {
      let callBack = fn || 'getDevCacheAllResult'
      try {
        window.hilink.getDevCacheAll('0', '', callBack)
        callbackFn && callbackFn()
      } catch (err) {
        console.warn(`getDevCacheAll错误信息: ${err}`)
        window[callBack](JSON.stringify(mockData))
      }
    },
    setDeviceInfo (obj, fn, msg) {
      let Msg = msg || `下发${fn}：`
      console.log(Msg, obj)
      try {
        callbackFn && callbackFn()
        window.hilink.setDeviceInfo('0', JSON.stringify(obj), fn)
      } catch (err) {
        console.warn(`${Msg}错误信息: ${err}`)
        let data = {}
        for (let key in obj) {
          data.sid = key
          data.data = obj[key]
        }
        setTimeout(_ => {
          debugCallback && debugCallback()
          window.deviceEventCallback(JSON.stringify(data))
        }, 500)
      }
    }
  }
}

// 适配大字体和各种分辨率屏幕
export function htmlFontSize () {
  var h = Math.max(document.documentElement.clientHeight, window.innerHeight || 0)
  var w = Math.max(document.documentElement.clientWidth, window.innerWidth || 0)
  var width = w > h ? h : w
  width = width > 720 ? 720 : width
  var fz = ~~(width * 100000 / 36) / 10000 / 10
  console.log('fz:' + fz)
  document.getElementsByTagName('html')[0].style.cssText = 'font-size: ' + fz + 'px'
  var realfz = ~~(+window.getComputedStyle(document.getElementsByTagName('html')[0]).fontSize.replace('px', '') * 10000) / 10000
  console.log('realfz:' + realfz)
  if (fz !== realfz) { document.getElementsByTagName('html')[0].style.cssText = 'font-size: ' + fz * (fz / realfz) + 'px' }
}



